<template>
    <div class="multi-checklist">
        <label class="multi-checklist-title">{{label}}</label>
        <div :class="showbox? 'multi-checklist-contentc':'multi-checklist-content'">
            <div class="option-item" v-for="(item,index) in options">
                <input type="checkbox" :id="'mchecklist'+index+curTime" :value="item.value" v-model="checkedVal"
                       class="checkbox" :disabled="item.disabled || disabled">
                <label :for="'mchecklist'+index+curTime" class="check-label">{{item.name}}</label>
            </div>
        </div>
    </div>
</template>

<script>
    /**
     * @author liuxin
     * @description  value
     * @param  label
     * @param  options [{value:'1',name:'1',disabled:false}] value:  name:checkbox  disabled:
     * @param  checked ['1']disabled:true
     * @param  disabled false
     * @param  showbox  ( false:)
     */
    export default {
        name: "multi-check-list",
        model: { // v-model  valuechange
            prop: 'checked',
            event: 'change'
        },
        props: { // 
            label: String,
            options: Array,
            checked: Array,
            disabled: false,
            showbox: {
                type: Boolean,
                default: true
            },
        },
        watch: { // 
            checkedVal(newValue, oldValue) {
                if (newValue != oldValue) {
                    this.$emit('change', newValue)
                }
            }
        },
        data() {
            return {
                checkedVal: this.checked, // 
                curTime: new Date().getTime(), // id
            }
        },
    }
</script>

<style scoped lang="scss">
    .multi-checklist {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 15px 5px;

        .option-item {
            margin-left: 5%;
        }

        .checkbox {
            display: none;

            &:disabled + .check-label, &:disabled + .check-label:before {
                opacity: .7;
            }
        }

    }

    .multi-checklist-title {
        width: max-content;
        max-width: 40%;
    }

    .multi-checklist-content {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        flex-wrap: wrap;

        .check-label {
            display: inline-block;
            padding: 2px 10px;
            height: 20px;
            line-height: 20px;
            border-radius: 3px;
            border: 1px solid #dfdfdf;
            background-color: #f2f2f2;
        }

        .checkbox:checked + .check-label {
            background-color: #fdeae8;
            border-color: #ef3e31;
            color: #ef3e31;
        }
    }

    .multi-checklist-contentc {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        flex-wrap: wrap;

        .check-label {
            padding: 0 0 0 5px;
            border: none;
            background-color: transparent;
            display: flex;
            align-items: center;

            &:before {
                content: '';
                width: 12px;
                height: 12px;
                display: inline-block;
                margin-right: 5px;
                border: 1px solid;
                text-align: center;
                line-height: 14px;
            }
        }

        .checkbox:checked + .check-label {
            color: #ef3e31;

            &:before {
                content: '';
            }
        }
    }
</style>
